// branding styles
#branding{

  margin-top: 60px;

  @media screen and (max-width: $xs-max-width) {
    margin-top: 0px;
  }

  img.main-logo {
    width: 45%;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 15vh;
      margin-bottom: 5vh;
    }

  }

  h2.big-title {
    margin-bottom: 40px;
    @media screen and (max-width: $xs-max-width) {
      margin-bottom: 30px;
    }
  }

  p.paragraph {
    margin-bottom: 20%;
    @media screen and (min-width: 540px) {
      font-size: 26px;
      line-height: 1.20em;
      margin-bottom: 15px;
    }
  }

  div.branding-heading {
    margin-bottom: 100px;
  }
}
