// read-more styles
a.read-more{
  color: $dark-color;
  font-size: 14px;
  letter-spacing: 0.3em;
  text-underline: none;

  @media screen and (max-width: $xs-max-width) {
    font-size: 8px;
  }

  &.bold {
    @include extra-bold-font();
  }

  &.regular {
    @include regular-font();
  }

  &:hover {
    text-decoration: none;
  }

}
