// about-landing styles
#about-landing {

  .main-logo {
    margin-top: 300px;
    margin-bottom: 400px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 150px;
      margin-bottom: 150px;
    }

  }

  .decoration {
    margin-top: 90px;
    .blue-bg {
      background-color: rgb(0,186,188);
      @include default-height(80%);
    }
  }

  .big-title {
    margin-top: 50px;
    margin-bottom: 30px;
    letter-spacing: 0.15em;
  }

  .simple-title {
    margin-top: 30px;
    margin-bottom: 50px;
    font-size: 34px;
    letter-spacing: 0.15em;
  }

  .blue-bg {
    @media screen and (min-width: $xs-max-width) {
      background-color: rgb(0,186,188);
    }

    p.paragraph {
      font-size: 24px;
      letter-spacing: normal;

      @media screen and (max-width: $xs-max-width) {
        font-size: 12px;
      }

      &:first-child {
        margin-top: 80px;
      }

      &:last-child {
        margin-bottom: 80px;
      }
    }
  }

}
