// main-footer styles
footer{

  &.main-footer,
  &.home-footer {
    margin-top: 300px;
    margin-bottom: 50px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
      margin-bottom: 10px;
    }

    .navigation {
      @media screen and (max-width: $xs-max-width) {
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }

    img.main-logo {
      @media screen and (min-width: $xs-max-width) {
        margin-top: 100px;
        margin-bottom: 200px;
        @include default-width(65%);
      }
      @include default-width(100%);
    }

    a.footer-link {
      @media screen and (max-width: $xs-max-width) {
        font-size: 6px;
      }
    }
  }

  &.main-footer {
    background: url("../images/general/general-footer-bg.png") no-repeat center center;
    background-size: contain;
  }

  &.home-footer {
    background: url("../images/home/home-footer-bg.png") no-repeat center center;
    background-size: contain;
  }

}
