// service styles
div.service{


  p.paragraph{
    text-align: justify;
    width: 100%;
    @media screen and (min-width: $xs-max-width) {
      font-size: 26px;
      line-height: 1.20em
    }
    @media screen and (max-width: $xs-max-width) {
      font-size: 11px;
      line-height: 0.95em;
      @include regular-font();
    }
  }
  h5.image-title,
  h3.simple-title{

    @media screen and (min-width: $xs-max-width) {
      width: 100%;
    }

    &.inverse {
      text-align: right;
    }

  }

  h5.image-title{
    margin-top: 10px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 10px;
    }
  }
}
