@mixin regular-font() {
  font-family: 'Open Sans';
  font-weight: normal;
  font-style: normal;
}

@mixin light-font() {
  font-family: 'Open Sans';
  font-weight: 300;
  font-style: normal;
}

@mixin light-italic-font() {
  font-family: 'Open Sans';
  font-weight: 300;
  font-style: italic;
}

@mixin bold-font() {
  font-family: 'Open Sans';
  font-weight: bold;
  font-style: normal;
}

@mixin bold-italic-font() {
  font-family: 'Open Sans';
  font-weight: bold;
  font-style: italic;
}

@mixin semi-bold-font() {
  font-family: 'Open Sans';
  font-weight: 600;
  font-style: normal;
}

@mixin italic-font() {
  font-family: 'Open Sans';
  font-weight: normal;
  font-style: italic;
}

@mixin semi-bold-italic-font() {
  font-family: 'Open Sans';
  font-weight: 600;
  font-style: italic;
}

@mixin extra-bold-font() {
  font-family: 'Open Sans';
  font-weight: 800;
  font-style: normal;
}

@mixin extra-bold-italic-font() {
  font-family: 'Open Sans';
  font-weight: 800;
  font-style: italic;
}

@mixin default-width($width) {
  max-width: $width;
  width: $width;
  min-width: $width;
}

@mixin default-height($height) {
  max-height: $height;
  height: $height;
  min-height: $height;
}

@mixin mt($size) {
  margin-top: $size;
}

@mixin mb($size) {
  margin-bottom: $size;
}

@mixin ml($size) {
  margin-left: $size;
}

@mixin mr($size) {
  margin-right: $size;
}

@mixin pt($size) {
  padding-top: $size;
}

@mixin pb($size) {
  padding-bottom: $size;
}

@mixin pl($size) {
  padding-left: $size;
}

@mixin pr($size) {
  padding-right: $size;
}
