// casa-cinco-landing styles
#casa-cinco-landing{

  margin-top: 60px;
  padding-top: 40px;
  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    margin-top: 0px;
    padding-top: 0px;
    margin-bottom: 50px;
  }

  .landing-title{

    h2.big-title {
      margin-top: 50px;
      @media screen and (max-width: $xs-max-width) {
        margin-top: 0px;
      }
    }

    img#casa-cinco-landing-image {
      @include default-width(110%);
      margin-top: 100px;
      @media screen and (max-width: $xs-max-width) {
        margin-top: 80px;
        margin-bottom: 20px;
      }

    }
    .shadow-lg{
      @include default-height(140vh);
      @media screen and (max-width: $xs-max-width) {
        @include default-height(auto);
      }
    }
  }

  .fg-pink {
    background-color: #ef509c;
    color: white;
    padding-left: 50px;
  }

  h3.simple-title {
    letter-spacing: 0.30em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 12px;
    }
  }

  h5.image-title {
    padding-right: 25px;
    letter-spacing: 0.45em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 8px;
      padding-right: 0px;
    }
  }

  p.paragraph {
    font-size: 26px;
    line-height: normal;
    margin-top: 80px;
    margin-bottom: 300px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 10px;
      line-height: normal;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  .landing-footer {
    img.center-footer-image {
      margin-top: 100px;
      @media screen and (max-width: $xs-max-width) {
        margin-top: 50px;
        margin-bottom: 10px;
      }
    }

    img.keep-scrolling {
      @include default-height(250px);
    }
  }

}

