// contact-form styles
div#contact-form{

  margin-top: 70px;

  input,textarea{
    font-size: 120px;
    @include light-font();
    border-radius: 0;
    border-top-style: none;
    border-left-style: none;
    border-right-style: none;
    border-color: $dark-color;
    text-align: center;
    @media screen and (max-width: $xs-max-width) {
      font-size: 50px;
    }
  }

  textarea#inputTextArea{
    min-height: 193px;
    height: 193px;
    @media screen and (max-width: $xs-max-width) {
      min-height: 60px;
      height: 60px;
      font-size: 40px;
    }
  }

  input {
    height: 175px;
    @media screen and (max-width: $xs-max-width) {
      height: 75px;
    }
  }



  #submit-group {
    margin-top: 300px;
    margin-bottom: 350px;
    @media screen and (max-width: $xs-max-width) {
      margin-top: 40px;
      margin-bottom: 40px;
    }
    button{
      width: 100%;
      font-size: 110px;
      background-color: white;
      border-color: $dark-color;
      color: turquoise;
      border-radius: 0;
      @include light-font();
      @media screen and (max-width: $xs-max-width) {
        font-size: 50px;
      }

    }
  }

  #input-text-area-form-group {
    margin-top: 200px;
    @media screen and (max-width: $xs-max-width) {
      margin-top: 40px;
    }
  }

  ::placeholder{
    color: lightgray;
    opacity: 0.25;
  }

  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: lightgray;
  }

  :-ms-input-placeholder { /* Microsoft Edge */
    color: lightgray;
  }

  label{
    @include light-font();
    font-size: 34px;
    letter-spacing: 0.15em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 18px;
    }
  }

}
