// truequeria-content styles
#truequeria-landing-content {
  margin-top: 100px;
  margin-bottom: 250px;

  @media screen and (max-width: $xs-max-width) {
    padding-top: 0px;
    margin-bottom: 100px;
  }

  div.horizontal-info {
    margin-top: 200px;
    margin-bottom: 200px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
      margin-bottom: 50px;
    }

    p.paragraph {
      @media screen and (max-width: $xs-max-width) {
        font-size: 12px;
        margin-top: 10px;
      }
    }
  }

  div.content-footer {

    margin-top: 300px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 70px;
    }

    h2.big-title{
      margin-bottom: 50px;
      margin-bottom: 15px;
    }

    .content-footer-center {
      padding-top: 50px;

      @media screen and (max-width: $xs-max-width) {
        padding-top: 0px;
      }

      p.paragraph{
        &:first-of-type {
          margin-top: 100px;
        }

        &:last-of-type {
          margin-bottom: 600px;
        }

        font-size: 26px;

        @media screen and (max-width: $xs-max-width) {
          margin-top: 10px;
          margin-bottom: 50px;
          font-size: 12px;
          line-height: 0.80em;
        }
      }

      img.title-logo{
        margin-top: 150px;
      }
    }

    div.footer-bg {
      background: url('../images/truequeria/truequeria-footer-bg.svg') no-repeat top center;
      background-size: contain;
      @media screen and (max-width: $xs-max-width) {
        background-size: 50% auto;
      }
    }

  }


}

