// truequeria-landing styles
#truequeria-landing {

  margin-top: 60px;

  padding-top: 40px;

  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    margin-top: 0px;
    padding-top: 10px;
    margin-bottom: 10px;
  }

  .landing-title{
    img#truequeria-landing-image {
      margin-top: 100px;
      margin-bottom: 100px;
      @include default-width(120%);
      @media screen and (max-width: $xs-max-width) {
        margin-top: 50px;
      }
    }
    .shadow-lg{
      h2.big-title {
        margin-top: 35px;
        margin-left: 35px;
      }
    }
  }

  h3.simple-title {
    letter-spacing: 0.30em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 12px;
    }
  }

  h5.image-title {
    padding-right: 25px;
    letter-spacing: 0.45em;
    @media screen and (max-width: $xs-max-width) {
      padding-right: 0px;
      font-size: 8px;
    }
  }

  p.paragraph {
    font-size: 26px;
    line-height: 1.20em;
    letter-spacing: 0.20em;
    margin-top: 80px;
    margin-bottom: 300px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 10px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

  .landing-footer {
    img.center-footer-image {
      margin-top: 100px;
      @media screen and (max-width: $xs-max-width) {
        margin-top: 20px;
      }
    }

    img.keep-scrolling {
      @include default-height(250px);
    }
  }

}

