// magueyro-content styles
#magueyro-landing-content {
  margin-top: 100px;
  margin-bottom: 25px;

  div.horizontal-info {
    margin-top: 250px;
    margin-bottom: 120px;

    p.paragraph {
      font-size: 22px;

      @media screen and (max-width: $xs-max-width) {
        font-size: 12px;
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }

    @media screen and (max-width: $xs-max-width) {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }

  div.column-images {

    margin-top: 220px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 0px;
    }

    img {

      @include default-width(100%);
      @media screen and (max-width: $xs-max-width) {
        margin-bottom: 20px;
      }
      @media screen and (min-width: $xs-max-width) {
        @include default-width(95%);
      }

    }

  }

  .image-row {
    margin-top: 100px;
    @media screen and (max-width: $xs-max-width) {
      margin-top: 0px;
    }
  }

  div.applications {

    margin-top: 250px;
    margin-bottom: 150px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 10px;
      margin-bottom: 10px;
    }

    @media screen and (min-width: $xs-max-width) {
      background: url('../images/magueyro/magueyro-applications-bg.svg') no-repeat bottom center;
      background-size: auto 85%;
    }

    div.applications-center {

      h2.big-title {
        margin-top: 30px;
        margin-bottom: 20px;
        @media screen and (max-width: $xs-max-width) {
          font-size: 22px;
        }
      }

      p.paragraph {
        margin-top: 80px;
        font-size: 22px;
        line-height: 1em;
        @media screen and (max-width: $xs-max-width) {
          font-size: 12px;
          margin-top: 20px;
        }
      }

      img.applications-logo{
        margin-top: 100px;
        margin-bottom: 30px;
        @include default-width(55%);
        @media screen and (max-width: $xs-max-width) {
          margin-top: 20px;
          margin-bottom: 10px;
        }
      }
    }

    img.contact-us-logo {
      @include default-width(50px);
    }

  }

}
