// contact-address styles
div#contact-address{

  margin-bottom: 150px;

  h3#we-are-here {
    transform: rotate(-90deg);
    transform-origin: 59% 314%;

    @media screen and (max-width: $xs-max-width) {
      transform-origin: 86% 475%;
    }

    text-transform: uppercase;
    @include bold-font();
    font-size: 45px;
    letter-spacing: 0.20em;
    width: 200%;

    @media screen and (max-width: $xs-max-width) {
      font-size: 16px;
    }

  }

  p {

    &:first-of-type {
      margin-top: 65px;
      @media screen and (max-width: $xs-max-width) {
        font-size: 16px;
      }
    }

    margin-bottom: 0px;
    font-size: 35px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 16px;
    }
  }

  hr {
    border-color: $dark-color;
    margin: 50px 0;
    @media screen and (max-width: $xs-max-width) {
      margin: 15px 60px 15px 0;
    }
  }

  h2 {
    @include bold-font();
    font-size: 60px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 20px;
    }
  }


}
