// casa-cinco-content styles
#casa-cinco-content {

  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    margin-bottom: 50px;
  }

  .horizontal-info {
    margin-top: 220px;
    margin-bottom: 220px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 20px;
      margin-bottom: 20px;
    }

    p.paragraph {
      font-size: 24px;
      @media screen and (max-width: $xs-max-width) {
        margin-top: 20px;
        font-size: 10px;
      }
    }

  }

  img.keep-scrolling{
    @include default-height(220px);
  }

  .image-row {
    padding-top: 200px;
    @media screen and (max-width: $xs-max-width) {
      padding-top: 0px;
    }
  }

  img.content-image {
    margin-top: 250px;
    margin-bottom: 250px;
    @media screen and (max-width: $xs-max-width) {
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

  .content-footer {
    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
    }
    h2.big-title {
      margin-bottom: 20px;
    }
    p.paragraph {
      margin-top: 100px;
      margin-bottom: 250px;
      font-size: 22px;
      line-height: normal;
      @media screen and (max-width: $xs-max-width) {
        font-size: 10px;
        margin-top: 30px;
        margin-bottom: 100px;
      }
    }
    img.center-footer-image{
      margin-bottom: 80px;
    }
    .footer-bg {
      @media screen and (min-width: $xs-max-width) {
        background: url("../images/cinco/cinco-content-footer-bg.svg") no-repeat bottom center;
        background-size: auto 90%;
      }
    }
  }

}
