// navbar styles
@import "../../../../atoms/components/basic/nav-link/nav-link";

nav.navbar {

  padding-top: 0;
  padding-bottom: 0;

  ul.navbar-nav{

    width: 100%;

    li.nav-item{

      width: 20%;

      a.nav-link{
        padding: 80px 0 0 0;
        color: #000000;
        font-size: 14px;
        @media screen and (max-width: $xs-max-width) {
          padding: 30px 0 0 0;
          font-size: 5px;
        }

      }

      img {
        @media screen and (max-width: $xs-max-width) {
          @include default-width(70%);
          margin-top: 0px;
        }
        @include default-width(45%);
      }
    }
  }
}
