// contact-us-link styles
div.contact-us-link{

  a.contact-us {

    color: $dark-color;
    text-transform: uppercase;
    @include bold-font();
    font-size: 32px;
    letter-spacing: 0.85em;
    width: 100%;

    @media screen and (max-width: $xs-max-width) {
      font-size: 14px;
    }

    &:hover{
      text-decoration: none;
    }

  }

}
