// home-work styles
#home-work{

  background: url('../images/home/home-work-background.svg') no-repeat top center;
  background-size: contain;
  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    background-size: 60% auto;
    background-position: center 20%;
    margin-bottom: 100px;
  }

  .section-title{
    margin-bottom: 150px;
    @media screen and (max-width: $xs-max-width) {
      margin-bottom: 50px;
    }
  }

  h4.work-title{
    text-transform: uppercase;
    @include regular-font();
    font-size: 20px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 12px;
    }
  }

  img.work-magueyro{
    max-width: 100%;
  }

  div.work-footer{
    margin-top: 150px;
    @media screen and (max-width: $xs-max-width) {
      margin-top: 75px;
    }
  }

  p.paragraph{
    width: 100%;
    padding: 10px 10px 40px 10px;
    background-color: lightgray;
    @include regular-font();
    margin-bottom: 30px;
  }



}
