// latest-projects styles
#lastest-projects{

  margin-bottom: 350px;

  @media screen and (max-width: $xs-max-width) {
    margin-bottom: 75px;
  }

  img.basic-logo{
    max-width: 80px;
  }

  h4.latest-project-title{
    @media screen and (max-width: $xs-max-width) {
      font-size: 14px;
      padding-top: 5px;
    }
  }

  .projects-list-bg {
    background: url('../images/general/general-project-list-bg.svg') no-repeat right center;
    background-size: contain;
  }

  .lastest-projects-list {
    padding-bottom: 50px;

    a.big-link{
      max-width: 100%;
      width: 100%;
      @media screen and (max-width: $xs-max-width) {
        font-size: 18px;
      }
    }

  }

}
