
div#magueyro-landing {

  margin-top: 60px;
  padding-top: 40px;
  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    margin-top: 0px;
    padding-top: 0px;
    margin-bottom: 20px;
  }

  .landing-title{

    img#magueyro-landing-image {
      @include default-width(130%);
    }
    .shadow-lg{
      padding-bottom: 50px;
    }
  }

  h3.simple-title {
    letter-spacing: 0.30em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 12px;
    }
  }

  h5.image-title {
    padding-right: 25px;
    letter-spacing: 0.45em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 8px;
      padding-right: 0px;
    }
  }

  p.paragraph {
    font-size: 26px;
    line-height: normal;
    margin-top: 80px;
    margin-bottom: 400px;

    @media screen and (max-width: $xs-max-width) {
      font-size: 12px;
      margin-top: 30px;
      margin-bottom: 30px;
      text-align: left;
    }
  }

  .landing-footer {
    img.center-footer-image {
      margin-top: 100px;
    }

    img.keep-scrolling {
      @include default-height(250px);
    }
  }

}
