// beer-landing styles
#beer-landing{

  margin-top: 60px;
  padding-top: 40px;
  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    margin-top: 0px;
    padding-top: 0px;
    margin-bottom: 70px;
  }

  .landing-title{
    img#the-beer-store-landing-image {
      @include default-width(130%);
    }
    .shadow-lg{
      padding-bottom: 50px;
    }
  }

  h3.simple-title {
    letter-spacing: 0.30em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 14px;
    }
  }

  h5.image-title {
    padding-right: 25px;
    letter-spacing: 0.45em;
    @media screen and (max-width: $xs-max-width) {
      font-size: 10px;
      padding-right: 0px;
    }
  }

  p.paragraph {
    font-size: 26px;
    line-height: normal;
    margin-top: 80px;
    margin-bottom: 300px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 10px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

  .landing-footer {
    img.center-footer-image {
      margin-top: 100px;
      @media screen and (max-width: $xs-max-width) {
        margin-top: 40px;
      }
    }

    img.keep-scrolling {
      @include default-height(250px);
    }
  }

}
