// beer-content styles
#beer-content {

  margin-bottom: 600px;

  @media screen and (max-width: $xs-max-width) {
    margin-bottom: 100px;
  }

  .horizontal-info {
    margin-top: 220px;
    margin-bottom: 110px;

    p.paragraph {
      font-size: 24px;

      @media screen and (max-width: $xs-max-width) {
        font-size: 12px;
        margin-top: 20px;
        margin-bottom: 20px;
      }

    }

    @media screen and (max-width: $xs-max-width) {
      margin-top: 20px;
      margin-bottom: 20px;
    }

  }

  img#chef-image {
    margin-top: 110px;
    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
      margin-bottom: 50px;
    }
  }

  .beer-divider {
    margin-top: 220px;
    margin-bottom: 220px;
    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
      margin-bottom: 50px;
    }
  }

  .content-footer {

    margin-top: 150px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
    }

    h2.big-title {
      margin-bottom: 30px;
    }

    p.paragraph {
      font-size: 24px;
      line-height: normal;
      &:first-of-type {
        margin-top: 100px;
      }

    }

    .info-line {


      .info-col {
        @media screen and (min-width: $xs-max-width) {
          background: url("../images/beer/beer-content-footer-bg.svg") no-repeat center right;
          background-size: contain;
          padding-top: 150px;
          padding-bottom: 120px;
        }

        p.paragraph {
          @media screen and (max-width: $xs-max-width) {
            font-size: 12px;
          }
          &:last-of-type {
            @media screen and (max-width: $xs-max-width) {
              margin-bottom: 50px;
            }
          }
        }
      }
      margin-bottom: 200px;
      @media screen and (max-width: $xs-max-width) {
        margin-bottom: 50px;
      }
    }

    img.contact-us-logo {
      @include default-height(300px);
      margin-top: 400px;
    }

  }

}
