// thanks-content styles
#thanks-content {
  padding-top: 20vh;
  background-color: rgb(0,186,188);
  @include default-height(100vh);

  .basic-logo {
    @include default-width(100px);
    margin-bottom: 60px;
  }

  h3.simple-title {
    @include bold-font();
    font-size: 28px;
    text-transform: none;
  }

  p.paragraph {
    font-size: 16px;
    margin-bottom: 60px;
  }

  a {
    color: $dark-color;
  }

}
