// clients styles
#clients {

  margin-bottom: 300px;

  @media screen and (max-width: $xs-max-width) {
    margin-bottom: 100px;
  }

  .shadow-lg {
    padding-top: 200px;
    padding-bottom: 200px;

    @media screen and (max-width: $xs-max-width) {
      padding-top: 40px;
      padding-bottom: 40px;
    }

    .big-title {
      margin-bottom: 20px;
    }

    p.paragraph {
      margin-top: 30px;
      margin-bottom: 200px;
      font-size: 22px;
      letter-spacing: normal;

      @media screen and (max-width: $xs-max-width) {
        font-size: 14px;
        margin-bottom: 30px;
      }
    }

    .client-image-container {

      @include default-height(200px);
      @media screen and (max-width: $xs-max-width) {
        @include default-height(100px);
      }

      .helper {
        display: inline-block;
        height: 100%;
        vertical-align: middle;
      }

      img.client-image {
        @include default-width(80%);
        @media screen and (max-width: $xs-max-width) {
          @include default-width(100%);
        }
      }
    }

  }

}
