// grupo-laser-landing styles
#grupo-laser-landing {

  margin-top: 60px;
  padding-top: 40px;
  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    padding-top: 0px;
    margin-top: 0px;
    margin-bottom: 40px;
  }

  .landing-title{

    h2.big-title {
      margin-top: 40px;
    }

    img#grupo-laser-landing-image {
      @include default-width(130%);
    }
    .shadow-lg{
      @media screen and (max-width: $xs-max-width) {
        padding-bottom: 70px;
      }
      padding-bottom: 120px;
    }
  }

  h3.simple-title {
    letter-spacing: 0.30em;
  }

  h5.image-title {
    padding-right: 25px;
    letter-spacing: 0.45em;

    @media screen and (max-width: $xs-max-width) {
      padding-right: 30px;
      font-size: 12px;
      text-align: right;
      margin-bottom: 25px;
    }
  }

  p.paragraph {
    font-size: 26px;
    line-height: 1.20em;
    letter-spacing: 0.20em;
    margin-top: 80px;
    margin-bottom: 300px;
    @media screen and (max-width: $xs-max-width) {
      font-size: 12px;
      margin-top: 0px;
      margin-bottom: 0px;
      text-align: left;
    }
  }

  .landing-footer {
    img.center-footer-image {
      margin-top: 100px;
    }

    img.keep-scrolling {
      @include default-height(250px);
    }
  }
}
