// project styles
div.project-content{

  img.project-image{
    width: 100%;
    max-width: 100%;
  }

  .image-row{
    margin-bottom: 50px;

    @media screen and (max-width: $xs-max-width) {
      margin-bottom: 10px;
    }

    &.left{
      a.read-more{
        margin-right: 25px;
      }
    }

    &.right{
      a.read-more{
        margin-left: 25px;
      }
    }
  }

  .text-row{

    a.big-link {
      @media screen and (max-width: $xs-max-width) {
        font-size: 22px;
      }
    }

    p.paragraph {
      @include regular-font();
      font-size: 26px;
      line-height: 1.20em;
      letter-spacing: 0.20em;
      @media screen and (max-width: $xs-max-width) {
        font-size: 10px;
        line-height: 1em;
      }
    }

  }

}
