// our-services styles
#our-services {

  margin-top: 80px;
  margin-bottom: 100px;

  @media screen and (max-width: $xs-max-width) {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .tiny-underline {
    margin-top: 30px;
  }

  .services {
    margin-top: 120px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 40px;
    }

    #digital-desing {
      padding-top: 150px;

      @media screen and (max-width: $xs-max-width) {
        padding-top: 40px;
      }
    }

    .big-title {
      font-size: 28px;
      margin-bottom: 20px;
      @media screen and (max-width: $xs-max-width) {
        font-size: 14px;
      }
    }

    .simple-title {
      font-size: 20px;
      text-transform: lowercase;
      @media screen and (max-width: $xs-max-width) {
        font-size: 10px;
      }
    }

  }

}
