// process styles
#about-process {

  margin-top: 200px;
  margin-bottom: 200px;

  @media screen and (max-width: $xs-max-width) {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .info-col {

    padding-top: 400px;

    @media screen and (max-width: $xs-max-width) {
      padding-top: 50px;
    }

    .big-title {
      margin-bottom: 20px;
    }

    p.paragraph {
      margin-top: 60px;
      font-size: 24px;
      letter-spacing: normal;
      @media screen and (max-width: $xs-max-width) {
        margin-top: 10px;
        font-size: 12px;
      }
    }

  }

  .process-list {

    background: url("../images/about/about-process.png") no-repeat top center;
    background-size: contain;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 100px;
    }

    .process {

      margin-top: 150px;
      margin-bottom: 150px;

      #process-1 {

      }

      #process-2 {

      }

      #process-3 {

      }

      #process-4 {

      }

      h5 {
        @include bold-font();
        font-size: 20px;
        margin-bottom: 0;
        @media screen and (max-width: $xs-max-width) {
          font-size: 10px;
        }
      }

      p {
        font-size: 14px;
        @media screen and (max-width: $xs-max-width) {
          font-size: 8px;
        }
      }

    }

  }

}
