// grupo-laser-content styles
#laser-content {

  .horizontal-info {

    margin-top: 220px;
    margin-bottom: 220px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
      margin-bottom: 50px;
    }

    p.paragraph {
      @media screen and (max-width: $xs-max-width) {
        font-size: 12px;
        margin-top: 10px;
      }
    }

  }

  .content-footer{

    margin-top: 300px;
    padding-top: 20px;
    margin-bottom: 150px;

    @media screen and (max-width: $xs-max-width) {
      margin-top: 50px;
      margin-bottom: 100px;
    }

    h2.big-title {
      margin-bottom: 20px;
    }
    p.paragraph {
      &:first-of-type {
        margin-top: 100px;
      }

      &:last-of-type {
        margin-bottom: 250px;
      }
      font-size: 22px;
      line-height: normal;

      @media screen and (max-width: $xs-max-width) {
        margin-top: 25px;
        margin-bottom: 40px;
        font-size: 10px;
        line-height: 0.80em;
        @include regular-font();
      }
    }
    img.main-logo{
      margin-bottom: 80px;
    }
    .footer-bg {
      @media screen and (min-width: $xs-max-width) {
        background: url("../images/laser/laser-content-footer-bg.svg") no-repeat bottom center;
        background-size: auto 90%;
      }
    }

  }

}
